.color-picker-container{grid-column:2/3;grid-row:2/3;display:grid;grid-template-columns:21px 6px 100px 12px 292px 10px 17px;grid-template-rows:27px 404px 13px 46px 21px 102px 17px;background-color:#d8d8d8}.color-picker-container .bitmap{grid-column:3/6;grid-row:2/3;background:linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, black 100%),linear-gradient(to right, white 0%, #5500ff 100%);cursor:crosshair}.color-picker-container .rectangle-color{grid-column:3/4;grid-row:4/5;background-color:#86c}.color-picker-container .hsl{grid-column:5/6;grid-row:4/5;padding:5px 57px 6px 10px;font-family:RobotoSlab;font-size:27px;font-weight:normal;font-stretch:normal;font-style:normal;line-height:normal;letter-spacing:normal;color:#000}.color-picker-container .btn-preview{display:flex;justify-content:space-around;align-items:center;grid-column:2/7;grid-row:6/7;color:#fff;background:linear-gradient(to bottom, #5500ff 30%, #7733ff 70%);border-radius:10px;border:5px solid #fff;outline:#50f solid 5px;font-size:30px;font-family:arvo;cursor:pointer;text-transform:lowercase}.color-picker-container .btn-preview:first-letter{text-transform:uppercase}.color-picker-container .btn-preview::before{content:"⬢";color:#fff;font-size:50px}.color-picker-container .btn-preview::after{content:"⬢";color:#fff;font-size:50px}